<template>
  <div id="app">
    <div class="container">
    	<div @click="routerToIndex" class="logo-text">
    		<img src="@/assets/public/images/logo.png" width="43px"/>
    		<span>圣杯网 - 分享你的智慧</span>
    	</div>
    	<div class="dividing_02"></div>
    	<div class="editor-info">
			 <el-select v-model="article.moduleId" placeholder="主题">
					<el-option
				      v-for="module in articleModules"
				      :key="module.id"
				      :label="module.name"
				      :value="module.id">
				    </el-option>
			  </el-select>
			  <el-select v-model="article.typeId" placeholder="类型">
			   		<el-option
				      v-for="type in articleTypes"
				      :key="type.id"
				      :label="type.name"
				      :value="type.id">
				    </el-option>
			  </el-select>
			</div>
			<div class="article-head">
			  <div class="article-title">
			  	<el-input
					  v-model="article.title"
					  maxlength="30"
  					show-word-limit
					  clearable>
					  <template slot="prepend">标题</template>
					</el-input>
			  </div>
			  <div calss="article-description">
					<el-input
						  type="textarea"
						  placeholder="文章介绍"
						  rows="3"
						  maxlength="200"
  						show-word-limit
						  v-model="article.description">
					<template slot="prepend">介绍</template>
					</el-input>
			  </div>
			</div>
      <div class="content">
        <mark-down @on-save="save" :theme="article.theme" :initialValue="article.content"></mark-down>
      </div>
      <div class="dividing_02"></div>
      <div class="editor-bottom">
	    	<el-button type="primary" @click.native="confirmSubmit" class="editor-button">提交</el-button>
	    </div>
    </div>
 </div>
</template>

<script>
//import MarkDown from "../build"; // 引入打包好的文件
//import MarkDown from 'vue-meditor';
  import MarkDown from '@/components/markdown/index'
  import {getModules} from '@/assets/appModules/modules'
  import {getTypes} from '@/assets/appModules/types'
  import doc from '@/assets/editor/doc'
  export default {
    name: "app",
    components: {
      MarkDown
    },
    data() {
    	let moduleId = 100
    	let articleModules = getModules(moduleId)
    	let articleTypes = getTypes(moduleId)
      return {
      	moduleId: 100,
        article: {
        	id: 0,
        	title: '',
        	description: '',
        	theme: 'OneDark',
        	content: '',
        	moduleId: articleModules[0].id, 
        	typeId: articleTypes[0].id,
        },
        articleModules: articleModules,
        articleTypes: articleTypes,
      };
    },
    methods: {
      save(res) {
      	if(window.localStorage){
      		this.article.content = res.value
      		localStorage.setItem('article', JSON.stringify(this.article))
	      	this.$message('已保存')
      	}else{
      		this.$message.warning({
	    			type: 'warning',
	    			message: '该浏览器无法在线保存!可以点击【保存到本地】'
	    		})
      	}

        console.log(res);
      },
		  confirmSubmit(){
		  	this.$confirm('是否确定提交?', '提示', {
		      confirmButtonText: '确定',
		      cancelButtonText: '取消',
		      type: 'info'
		    }).then(() => {
		     this._submit();
		    })
		  },
		  routerToIndex(){
		  	if(!window.localStorage)
			  	this.$confirm('系统将不会保存，确认退出?', '提示', {
			      confirmButtonText: '确定',
			      cancelButtonText: '取消',
			      type: 'warning'
			    }).then(() => {
			     	this.$router.push('/index/main')
			    })
			   else
			    this.$router.push('/index/main')
		  },
		  _submit(){
      	(async () => {
				try{
					let data = {
						moduleId: this.article.moduleId,
						typeId: this.article.typeId,
						title: this.article.title,
						description: this.article.description,
						content: this.article.content,
						theme: this.article.theme,
					}
					if (!data.title ||!data.content){
							throw '标题和内容不能为空'
					}
					let res;
					if (this.article.id) {
						data.id = this.article.id	
						res = await this.$api.user.editArticle(data);
					}else {
						res = await this.$api.user.submitArticle(data);
					}
					console.log(res);
					if(res.status === 2000){
						this.$message({
							dangerouslyUseHTMLString: true,
							type: 'success',
							message: '<h2>提 交 成 功</h2>',
						})
						localStorage.removeItem('article')
						this.$router.push('/web' + '/' +res.articleId)
					}else{
						this.$message.error(res.message || '提交失败!可以点击【保存到本地】');
					}
				}catch(e){
					let message = ''
					typeof(e) == 'string' ? message=e : message='提交失败!可以点击【保存到本地】'
					this.$message.error(message);
					console.log(e);
					}
				})()
     },
   },
    created() {
    	if(!window.localStorage)
    		this.$message.warning({
    			type: 'warning',
    			message: '该浏览器无法在线保存!可以点击【保存到本地】'
    		})
    	if (this.$route.params.id){
	    	(async () => {
					try{
						let res = await this.$api.web.getArticle({id: this.$route.params.id});
						if(res.status === 2000){
							this.article = res.article
						}else{
							this.$message.error('服务器繁忙');
						}
					}catch(e){
						this.$message.error('服务器繁忙');
						console.log(e);
					}
				})()
			} else if (this.moduleId == this.moduleId && localStorage.getItem('article')){
					this.article = JSON.parse(localStorage.getItem('article'))
					this.$message('已显示保存内容')
			} else{
	      setTimeout(() => {
	        this.article.content = doc;
	      }, 0);
      }
    }
  };

</script>

<style scoped lang="scss">
	@import '@/assets/public/css/font.scss';
  * {
    box-sizing: border-box;
  }
  .logo-text{
  	@include editor-head-font;
  	padding: 5px;
    cursor: pointer;
  }
  .container {
    width: 1200px;
    margin: 0 auto;
    ul {
      list-style: none;
    }
    .content {
      border: 1px solid #ccc;
    }
  }
  .editor-bottom{
  	text-align: right;
  	margin: 20px 10px 20px 0px;
  	.editor-button{
			padding: 12px 20px;
		}
  }
  .editor-info{
  	
  }
  .article-head{
  	margin-bottom: 10px;
  	padding-right: 100px;
  }
</style>



