const types = [
  {
    id: 100,
    name: 'web',
    children:[
    {
      id: 1,
      name: '一般',
    },
     {
      id: 2,
      name: '精选推荐',
    }]
  }]

function getTypes(id){
  for(let i=0,len=types.length; i<len; i++){
    if(id == types[i].id)
      return types[i].children
    return null
  }
}

export {getTypes}


